'use strict';

/**
* Number of significand bits for a single-precision floating-point number.
*
* @module @stdlib/constants-float32-num-significand-bits
* @type {integer32}
*
* @example
* import FLOAT32_NUM_SIGNIFICAND_BITS from '@stdlib/constants-float32-num-significand-bits';
* // returns 23
*/


// MAIN //

/**
* Number of significand bits for a single-precision floating-point number.
*
* @constant
* @type {integer32}
* @default 23
*/
var FLOAT32_NUM_SIGNIFICAND_BITS = 23|0;


// EXPORTS //

export default FLOAT32_NUM_SIGNIFICAND_BITS;
